function QuestionsModule() {
  const [openedCard, setOpenedCard] = useState(null);
  const { lang, changeLang, t } = useLang();

  const cards = [
    {
      header: t("qHeader1"),
      isOpened: true,
      description: t("qDescr1"),
      key: 0,
    },
    {
      header: t("qHeader2"),
      isOpened: true,
      description: t("qDescr2"),
      key: 1,
    },
    {
      header: t("qHeader3"),
      isOpened: true,
      description: t("qDescr3"),
      key: 2,
    },
    {
      header: t("qHeader4"),
      isOpened: true,
      description: t("qDescr4"),
      key: 3,
    },
  ];
  return (
    <Row className="mt-2  gx-3 gy-3">
      <Col xs={12} md={12} lg={6}>
        <Row xs={1} className="gy-2">
          {cards.map((el) => (
            <Col>
              <QuestionCard
                {...el}
                isOpened={el.key === openedCard}
                setOpened={setOpenedCard}
                index={el.key}
                key={el.key}
              />
            </Col>
          ))}
        </Row>
      </Col>
      <Col xs={12} md={6} lg={3}>
        <QuestionBlock
          header={t("qCardHeader1")}
          description={t("qCardDescr1")}
          linkText={t("qCardLink1")}
          link={"https://wiki.qwins.co/"}
        />
      </Col>
      <Col xs={12} md={6} lg={3}>
        <QuestionBlock
          header={t("qCardHeader2")}
          description={t("qCardDescr2")}
          linkText={t("qCardLink2")}
          link={"https://t.me/qwinshelp"}
        />
      </Col>
    </Row>
  );
}

function CountryCard({ text, price, header, tariff, displayParams }) {
  const { s } = useTheme();
  const { t } = useLang();

  return (
    <div
      className={styles.countryCard}
      style={{ ...s.DCProsCard, height: "auto" }}
    >
      <div
        className={styles.countryHeaderContainer}
        style={s.VSCountryCardHeaderContainer}
      >
        <div
          className={styles.countryBlackEl}
          style={{ backgroundColor: s.DCProsHeader }}
        >
          <h4 style={{ fontSize: "17px", margin: "0", color: s.guideCard2Bg }}>
            {header}
          </h4>
        </div>
        <p style={{ color: s.DCProsHeader }}>{price}</p>
      </div>

      <div className="mt-3" style={{ width: "100%" }}>
        {text.map((el, index) => (
          <div
            key={index}
            className={styles.countryParamsRow}
            style={{
              display: "flex",
              width: "100%",
              overflow: "hidden",
              textAlign: "left",
            }}
          >
            <p
              className={styles.countryParamsHeader}
              style={{
                color: s.VSCountryCardVal,
                marginLeft: "15px",
                whiteSpace: "normal",
                wordBreak: "break-word",
                overflowWrap: "break-word",
                color: s.DCProsText,
              }}
            >
              • {el}
            </p>
          </div>
        ))}
        <h6
          style={{
            textAlign: "left",
            marginTop: "10px",
            fontSize: "18px",
            color: s.cardBlinkColor,
          }}
        >
          {t("VHTariffHeader")}
        </h6>
        {tariff.map((el, index) => (
          <div
            key={index}
            className={styles.countryParamsRow}
            style={{
              display: "flex",
              width: "100%",
              overflow: "hidden",

              textAlign: "left",
            }}
          >
            <p
              className={styles.countryParamsHeader}
              style={{
                color: s.VSCountryCardVal,
                marginLeft: "15px",
                whiteSpace: "normal",
                wordBreak: "break-word",
                overflowWrap: "break-word",
                color: s.DCProsText,
              }}
            >
              • {el}
            </p>
          </div>
        ))}
      </div>

      <a href="https://my.qwins.co" style={{ textDecoration: "none" }}>
        <button
          className={`${styles.countryBuyBtn} mt-3`}
          style={{
            color: s.guideCard2Bg,
            ...s.VSMainBannerBtnHover,
          }}
        >
          {t("VSBuyBtnText")}
        </button>
      </a>
    </div>
  );
}
function CountryCards() {
  const { lang, changeLang, t } = useLang();
  const cards = t("VHTariffs");
  return (
    <div className="mt-5">
      <Row
        xs={1}
        md={2}
        xxl={4}
        className={`gy-4 ${cards.length === 1 ? "justify-content-center" : ""}`}
      >
        {cards.map((el, index) => {
          return (
            <Col>
              <CountryCard {...el} key={index} />
            </Col>
          );
        })}
      </Row>
    </div>
  );
}

function Pros2Card({ header, description, picSrc, children }) {
  const { s, toggleTheme } = useTheme();

  const descriptionClass = `${styles.cardDescr}`;
  const headerClass = `${styles.cardHeader} m-0`;
  const divClass = `${styles.card} h-100 ${styles.pros2CardStructure}`;
  return (
    <Col>
      <div
        className={divClass}
        style={s.cardBg}
        //   style={{ paddingBottom: "7px" }}
      >
        <Row>
          <Col xs={12}>
            <div className="d-flex align-items-center gap-3">
              <div>
                <div className="panel-container" style={s.card2IconBG}>
                  {children}
                </div>
              </div>
              {/* <img src={picSrc} alt="IMG" className={styles.prosSmallGif} /> */}
              <h4
                className={headerClass}
                style={{ fontSize: "22px", ...s.cardHeader }}
              >
                {header}
              </h4>
            </div>
          </Col>
          <Col xs={12}>
            <p
              className={descriptionClass}
              style={{ marginTop: "28px", ...s.card2Descr }}
              // style={{ maxWidth: "82%" }}
            >
              {description}
            </p>
          </Col>
        </Row>
      </div>
    </Col>
  );
}

