export default function VirtualHostingPage({ windowSize }) {
  const { lang, changeLang, t } = useLang();
  const { s, toggleTheme } = useTheme();

  return (
    <>
      <MainBanner isLg={windowSize.isLg} isXl={windowSize.isXl} />
      <ProsModule2 isXl={windowSize.isXl} />
      <MinorHeader
        header={t("VHMinorHeader")}
        isXs={windowSize.isXs}
        isSm={windowSize.isSm}
      />
      <CountryCards />
      <div style={{ textAlign: "left" }}>
        <h4
          className={`${styles.minorHeader} mt-5 mb-0`}
          style={{
            ...s.minorHeader,
            fontFamily: "Gilroy-semibold",
            fontSize: "clamp(25px, 5vw, 28px)",
          }}
        >
          {t("qMinorHeader")}
        </h4>
      </div>

      <QuestionsModule />
    </>
  );
}
